* {
  border: 0;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

form {
  container: form/inline-size;
  margin: auto;
  padding: 1.5em;
  width: 100%;
  max-width: 36em;
}

.steps {
  background-color: var(--app-color-white-shade) !important;
  border-radius: 0.75em;
  display: flex;
  padding: 1.5em;
  flex-direction: column;
  justify-content: center;
  width: 100%;
}
.steps__connector,
.steps__step {
  position: relative;
}
.steps__connector {
  background-color: var(--app-color-white);
  margin-inline-start: 0.75em;
  width: 0.125em;
  height: 1.25em;
  transform: translateX(-50%);
  transition: background-color var(--trans-dur);
}
.steps__connector:before {
  background-color: var(--app-color-secondary);
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  transform: scale(1, 0);
  transform-origin: 50% 0;
  transition: background-color var(--trans-dur),
    transform var(--trans-dur) var(--trans-timing);
}
.steps__step {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  z-index: 1;
}
.steps__step-name {
  color: var(--app-color-secondary);
  font-size: 0.75em;
  line-height: 2;
  transition: color var(--trans-dur) var(--trans-timing),
    font-weight var(--trans-dur) var(--trans-timing);
}
.steps__step-number {
  background-color: var(--app-color-white);
  color: var(--app-color-secondary);
  border-radius: 50%;
  margin-inline-end: 0.5em;
  text-align: center;
  width: 2.5em;
  height: 2.5em;
  padding-top: 0.5em;
  transition: background-color var(--trans-dur) var(--trans-timing),
    box-shadow var(--trans-dur) var(--trans-timing);
}

/* Change layout depending on form width */
@container form (min-width: 30em) {
  .steps {
    flex-direction: row;
    align-items: center;
    padding: 1.5em 2.25em 2.25em 2.25em;
  }
  .steps__connector {
    margin-inline-start: 0;
    width: 100%;
    height: 0.125em;
    transform: translateY(-50%);
  }
  .steps__connector:before {
    transform: scale(0, 1);
    transform-origin: 0 50%;
  }
  [dir="rtl"] .steps__connector:before {
    transform-origin: 100% 50%;
  }
  .steps__step-name {
    position: absolute;
    top: 100%;
    left: 50%;
    text-align: center;
    width: 9em;
    transform: translateX(-50%);
  }
  .steps__step-number {
    margin-inline-end: 0;
  }
}

.passed-step {
  background-color: var(--app-color-secondary) !important;
  color: var(--app-color-white) !important;
}

.active-step {
  background-color: var(--app-color-secondary) !important;
  color: var(--app-color-white) !important;
  height: 3.5em;
  width: 3.5em;
  padding-top: 1em;
}

.passed-step-connector {
  background: var(--app-color-secondary) !important;
}
